import './about.scss';

import React, { useContext, } from 'react';
import { useTranslation, } from 'react-i18next';

import { ThemeContext, } from '../theme';
import Page from '../components/Page';
import { NavSecondPageBackTo } from "../components/elements/NavSecondPage";

import ScreenStart from '../screens/ScreenStart';
import ScreencCrriculumVitae from '../screens/ScreenCurriculumVitae';

import SocialLinks from '../components/SocialLinks';

import ImageMy from '../assets/images/my/my_2.png';



export default function AboutPage() {
  const { theme, } = useContext(ThemeContext);
  const { i18n, t, } = useTranslation('about');

  const imagePrefix = "https://ivannikov.pro";
  const imageUrl = imagePrefix + `/ivannikov.pro_${i18n.language}.jpg`;

  return (
    <Page
      title={t('About')}
      description={t('aboutDescription')}
      keywords={t('keywordsList')}
      meta={[
        { name: 'image', content: imageUrl, },
        { name: 'og:image', property: 'og:image', content: imageUrl, },
        { name: 'twitter:image', property: 'twitter:image', content: imageUrl, },
      ]}
    >
      <div id={`front ${theme}`} style={{ minHeight: '95vh', }}>
        <main className='main'>
          <ScreenStart
            //preTitle={}
            title={t('Aleksandr Ivannikov')}
            description={t('aboutDescription')}
            descriptionComponent={<>
              <SocialLinks /><br />
            </>}
            imageComponent={<img
              alt={`${t('Aleksandr Ivannikov')}`}
              title={`${t('Aleksandr Ivannikov')}`}
              placeholder='blurred'
              src={ImageMy}
            />}
          />

          <ScreencCrriculumVitae />
        </main>
      </div>
    </Page>
  );
}
