import './ScreenStart.scss';

import React from 'react';
import { useTranslation, } from 'react-i18next';

import { useModal, } from 'react-simple-modal-provider';

import CommonButton from '../components/CommonButton';

//import ImageMain from '../assets/images/main.svg';
import ImageMain from '../assets/images/main.webp';



export default function ScreenStart({
  preTitle, title, description,
  descriptionComponent,
  imageComponent,
}) {
  const { t, } = useTranslation('translation');
  const { open: openGetInTouchModal, } = useModal('GetInTouchModal');

  return (
    <section id='section-start' className='section section-start'>
      <div className='container'>
        <div className='row'>
          <div className='col-12 col-md-7 col-lg-6 col-xl-6 relative'>
            <div className='pre-title'>{preTitle && <span>{preTitle}</span>}</div>
            <h1 className=''>{title}</h1>

            <p className='desc'>{description}</p>
            {descriptionComponent && descriptionComponent}

            <div className='start-btns'>
              <CommonButton
                variant='gradient'
                size='static'
                className='btn'
                onClick={openGetInTouchModal}
              >{t('Get in Touch')}</CommonButton>
            </div>
          </div>
          <div className='col-12 col-md-5 col-lg-6 col-xl-6'>
            <div className='start__image'>
              {imageComponent
                ? imageComponent
                : <img
                  alt={t('IVANNIKOV.PRO')}
                  title={t('IVANNIKOV.PRO')}
                  src={ImageMain}
                />}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
